/** @jsx jsx */
import { jsx } from "theme-ui";
import slugify from "slugify";
import { Cell } from "@src/components/Grid";
import { connectHighlight } from "react-instantsearch-dom";
import { rem } from "@src/theme";
import { Link } from "gatsby";

import PlainCard from "@components/PlainCard";

import TypeTag from "@components/TypeTag";
import { trimSentence, formatCraftType } from "@src/helpers/utils";
import * as _ from "lodash";
const Highlight = ({ highlight, attribute, hit }) => {
  const parsedHit = highlight({
    highlightProperty: "_highlightResult",
    attribute,
    hit,
  });

  return (
    <span>
      {parsedHit.map((part, index) =>
        part.isHighlighted ? (
          <mark
            key={index}
            sx={{
              bg: "csiBlue",
              borderBottom: "1px dashed #616161",
            }}
          >
            {part.value}
          </mark>
        ) : (
          <span key={index}>{part.value}</span>
        )
      )}
    </span>
  );
};
const CustomHighlight = connectHighlight(Highlight);

const ResourceCard = (props) => (
  <Cell>
    <article
      sx={{
        mb: [rem(40), null, rem(40)],
        borderTop: "2px dotted #616161",
        h1: {
          textDecoration: "underline",
          textDecorationColor: "white",
          "&:hover": {
            textDecorationColor: "black",
            transition: "text-decoration-color 250ms ease-in-out",
          },
        },
      }}
    >
      <div
        sx={{
          mt: [rem(25), null, rem(40)],
          mb: rem(4),
        }}
      >
        <TypeTag type={props.hit.type.toLowerCase()} />
      </div>

      <Link
        to={`${slugify(formatCraftType(props.hit.type)).toLowerCase()}/${
          props.hit.slug
        }`}
      >
        <h1
          sx={{
            variant: "typography.headingL",
            mb: [rem(10), null, rem(10)],
            mt: [rem(10), null, rem(10)],
          }}
        >
          {props.static ? (
            props.hit.title
          ) : (
            <CustomHighlight attribute="title" hit={props.hit} />
          )}
        </h1>
      </Link>

      {props.hit.video && (
        <div>
          <iframe
            width="100%"
            height="169"
            src={props.hit.video}
            frameBorder="0"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            allowFullScreen
          ></iframe>
        </div>
      )}
      {props.hit.img && (
        <div>
          <img
            src={props.hit.img.src}
            alt={props.hit.img.alt}
            sx={{
              width: "100%",
            }}
          />
        </div>
      )}
      {props.hit.description &&
        props.hit.type !== "Video" &&
        props.hit.type !== "Infographic" &&
        props.hit.type !== "Insight" && (
          <p
            sx={{
              variant: "typography.paragraphXS",
            }}
          >
            {trimSentence(props.hit.description, 120)}
          </p>
        )}
      <ul
        sx={{
          mt: rem(24),
        }}
      >
        {props.hit.socialImpacts.length
          ? props.hit.socialImpacts
              .slice(0, 1)
              .map((t) => (
                <PlainCard
                  link={`explore/social-impacts/${slugify(
                    t.toLowerCase().replace(/[^\w\s]/gi, "")
                  )}`}
                  label={t}
                  size="xs"
                />
              ))
          : null}

        {props.hit.industries.length
          ? props.hit.industries
              .slice(0, 1)
              .map((t) => (
                <PlainCard
                  link={`explore/industries/${slugify(
                    t.toLowerCase().replace(/[^\w\s]/gi, "")
                  )}`}
                  label={t}
                  size="xs"
                />
              ))
          : null}
      </ul>
    </article>
  </Cell>
);

export default ResourceCard;
